@use "../abstracts/" as *;

body {
  margin: 0;
  padding: 0;
  font-family: var(--ff-primary);

  background-color: var(--color-100);
  min-height: 100vh;
  line-height: 1.5;

  @include breakpoint(tablet) {
    font-size: 20px;
  }
  @include breakpoint(laptop) {
    font-size: 24px;
  }
}
